          </HeadOperationItem>
          <HeadOperationItem name="export" @onOprationItem="exportTable">
            <i slot="operation-icon" class="icon-export"></i>
            <span slot="operation-name">导出</span>
          </HeadOperationItem>
        </div>
        <!-- <header-buttons :showRefresh="true" @buttonClickEvent="buttonClickEvent" cssType="1"></header-buttons> -->
      </HeaderTitle>
    </el-header>
    <header-search :show-primary-search="true" @refreshTableData="refreshTableData" cssType="1">
    </header-search>
    <el-main class="view eq" style='overflow-y:visible !important;'>
      <div class="searchDiv">
        <!-- <span>车间名称：</span>
        <el-select v-model="groupId" placeholder="请选择" size='mini'>
          <el-option v-for="item in groupOptions" :key="item.groupId" :label="item.groupName"
            :value="item.groupId"></el-option>
        </el-select>
        <span style="margin: 15px;">设备类型：</span>
        <el-select v-model="typeId" placeholder="请选择" size='mini'>
