        <span style="margin: 15px;">设备名称：</span>
        <el-input size='mini' v-model="deviceName" style="width: 180px;"></el-input> -->
        <span style="margin: 5px;">日期范围</span>
        <el-date-picker :clearable="false" class="dateTime" v-model="dateTime" type="datetimerange" align="left"
          size="mini" unlink-panels value-format="yyyy-MM-dd HH:mm:ss" :range-separator="BTN_TEXT.TO"
          :start-placeholder="BTN_TEXT.STARTTIME" :end-placeholder="BTN_TEXT.ENDTIME"
          :default-time="['00:00:00', '23:59:59']" :picker-options='pickerOptions'></el-date-picker>
        <!-- <span>班次范围</span>
        <el-select v-model="shiftData" size="mini" placeholder="请选择">
          <el-option v-for="item in shiftOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select> -->
        <el-button type="default" style="margin-left:5px;" size="mini" @click="findList()">查询</el-button>
        <el-button type="default" style="margin-left:5px;" size="mini" @click="reset()">重置</el-button>
      </div>
      <!-- main table -->
      <el-row class="tableShow">
        <el-col :span="24">
          <VXETable ref="xGrid" :tableName="''" :height="$options.filters.getTableHeight(260)"
            :tableLoading="mainPage.tableLoading" :tableDatas="mainPage.data" :tableColumns="mainPage.columns"
            :highlightCurrentRow="true" :tablePage="mainPage.page" @sortChange="sortChange"
            @onTablePageChange="handleMainPageChange">
          </VXETable>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>
/*
 * @Author: zhxy
 * @Date: 2021-08-12 09:05:28
 * @LastEditors: ZJM
 * @LastEditTime: 2022-05-22 14:28:53
 * @Description: file content
 */
// import { CommonService } from '@/services/common.service';
// 引入通用接口
import { searchCondetionFunc } from '@/utils/publicFunction';
// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 引入table组件
import VXETable from '@/components/common/Table';

import {
  EQUIPMENT_DATA,
  BTN_TEXT,
  PAGE_TITLE,
  ENERGY,
  MAINTAINTASK_STATUS,
  EQUIPMENT_MANAGEMENT_REPORT,
  // EQUIPMENT_STATUS_REPORT,
  STATUS_CODE,
  TITLE,
  MSG,
  VARIABLE
} from '@/utils/constantsFile';
// message service
import { showWarningMessage } from '@/utils/message';
import { getProductionCycleStatistics, configurationGetConfigurationByEnum } from '@/api/statusEquipmentReport.js';
import { GetList } from '@/api/equipmentmonitoring';
// 提示信息
import { showErrorNotify } from '@/utils/notify';

export default {
  name: 'EquipmentStatus',
  components: {
    VXETable,
    HeaderTitle,
    HeadOperationItem
  },
  inject: ['reload'],
  data() {
    return {
      VARIABLE: VARIABLE,
      MAINTAINTASK_STATUS: MAINTAINTASK_STATUS,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      BTN_TEXT: BTN_TEXT,
      PAGE_TITLE: PAGE_TITLE,
      ENERGY: ENERGY,
      TITLE: TITLE,
      MSG: MSG,
      maxHeight: document.body.clientHeight - 250,
      activeTab: 0,
      titleName: '',
      // groupId: '',
      // typeId: '',
      deviceName: '',
      groupOptions: [],
      typeOptions: [],
      mainPage: {
        show: true,
        page: {
          total: 0,
          currentPage: 1,
          pageSize: 10,
          align: 'right',
          pageSizes: [10, 20, 50, 100, 200, 500],
          layouts: [
            'Sizes',
            'PrevPage',
            'Number',
            'NextPage',
            'Total'
          ],
          perfect: true
        },
        data: [],
        columns: [
          {
            field: 'workshop',
            title: EQUIPMENT_MANAGEMENT_REPORT.WORKSHOP_NAME,
            width: 'auto',
            align: 'center'
            // fixed: 'left'
          },
          {
            field: 'typeName',
            title: EQUIPMENT_MANAGEMENT_REPORT.DEVICE_TYPE,
            width: 'auto',
            align: 'center'
            // sortable: true
            // fixed: 'left'
          },
          {
            field: 'deviceName',
            title: EQUIPMENT_MANAGEMENT_REPORT.DEVICE_NAME,
            width: 'auto',
            align: 'center'
            // sortable: true
            // fixed: 'left'
          },
          {
            field: 'number',
            title: EQUIPMENT_MANAGEMENT_REPORT.EQUIPMENT_NO,
            width: 'auto',
            align: 'center'
            // sortable: true
            // fixed: 'left'
          },
          // {
          //   field: 'typeName',
          //   title: EQUIPMENT_MANAGEMENT_REPORT.DEVICE_TYPE,
          //   width: 'auto',
          //   align: 'center',
          //   sortable: true
          //   // fixed: 'left'
          // },
          // {
          //   field: 'idleTimeSum',
          //   title: '总空闲时间(分钟)',
          //   width: 'auto',
          //   align: 'center'
          //   // formatter: this.propertyFormat
          //   // headerClassName: 'run'
          //   // fixed: 'left'
          // },
          {
            field: 'artifactNum',
            title: this.$store.state.pageInfo.pageConfigInfo.yieldKey + '序号',
            width: 'auto',
            align: 'center'
            // formatter: this.propertyFormat
            // headerClassName: 'run'
            // fixed: 'left'
          },
          {
            field: 'productionNum',
            title: this.$store.state.pageInfo.pageConfigInfo.yieldKey,
            width: 'auto',
            align: 'center'
          },
          {
            field: 'recordStartTime',
            title: '开始时间',
            width: 'auto',
            align: 'center'
          },
          {
            field: 'recordEndTime',
            title: '结束时间',
            width: 'auto',
            align: 'center'
          },
          {
            field: 'productionCycle',
            title: '加工时长(秒)',
            width: 'auto',
            align: 'center',
            formatter: ({ cellValue }) => {
              return Number((cellValue / 1000).toFixed(0));
            }
          }
          // {
          //   field: 'idleProductAvgTime',
          //   title: '平均生产单件空闲时间(秒)',
          //   width: 'auto',
          //   align: 'center'
          //   // headerClassName: 'run',
          //   // formatter: this.hourFormat
          //   // fixed: 'left'
          // },
          // {
          //   field: 'productionValue',
          //   title: '时间段产量',
          //   width: 'auto',
          //   align: 'center'
          //   // headerClassName: 'run',
          //   // formatter: this.hourFormat
          //   // fixed: 'left'
          // }
        ],
        // table是否加载
        tableLoading: false
      },
      isSearchBoxShow: false,
      orderByData: {},
      searchList: [],
      // 指标区间查询参数
      specialSearchList: [],
      searchData: {
        conditionJson1: []
      },
      pickerOptions: {
        disabledDate: (current) => {
          return current.getTime() > this.moment(`${this.moment(Date.now()).format('YYYY-MM-DD')} 23:59:59`).format('x');
        }
      },
      dateTime: [],
      dateTime2: [new Date(2016, 9, 10, 0, 0), new Date(2016, 9, 10, 23, 59)],
      // 保存搜索内输入的搜索条件，防止翻页时变回无条件查询
      savedSearchCondition: [],
      // 班次列表
      shiftOptions: [],
      // 班次值
      shiftData: null
    };
  },
  watch: {
    // groupId(val) { },
    activeTab: function() {
      this.getQueryParameter();
      this.searchData = {
        conditionJson1: []
      };
      this.findList();
    }
  },
  methods: {

    // 按钮触发事件
    buttonClickEvent(type) {
      this[`${type}`]();
    },
    showSearchCard() {
      this.isSearchBoxShow = !this.isSearchBoxShow;
    },
    reset() {
      // this.groupId = '';
      // this.typeId = '';
      this.deviceName = '';
      this.mainPage.currentPage = 1;
      this.mainPage.pageSize = 10;
      this.findList();
    },
    handleMainPageChange(currentPage, pageSize) {
      this.mainPage.page.currentPage = currentPage;
      this.mainPage.page.pageSize = pageSize;
      this.findList();
    },
    refresh() {
      this.findList();
    },
    // 查询数据
    async findList() {
      // if(this.dateTime)
      try {
        this.mainPage.tableLoading = true;

        var conditionJson = {
          conditionJson1: [],
          operatorGroup: 1,
          conditionJson2: []
          // groupType: this.type,
          // workShopId: this.groupId,
          // size: this.mainPage.page.pageSize,
          // current: this.mainPage.page.currentPage
        };
        if (this.savedSearchCondition.length > 0) {
          conditionJson = searchCondetionFunc(...this.savedSearchCondition);
        }
        var param = {
          // shiftStart: '',
          // shiftEnd: '',
          size: this.mainPage.page.pageSize,
          current: this.mainPage.page.currentPage,
          startTime: '',
          endTime: ''
        };

        if (this.dateTime) {
          param.startTime = this.dateTime[0];
          param.endTime = this.dateTime[1];
        }
        console.log(this.shiftData, this.shiftOptions, this.shiftOptions[this.shiftData]);
        if (this.shiftData !== null) {
          // eslint-disable-next-line no-unused-expressions
          // param.shiftStart = this.shiftOptions[this.shiftData].shiftStart;
          // param.shiftEnd = this.shiftOptions[this.shiftData].shiftEnd;
          param.shiftStart = conditionJson.size;
          param.shiftEnd = conditionJson.current;
        }
        console.log(this.dateTime, this.dateTime2);
        // const data = await deviceIdleGetIdleProductReport(param, conditionJson);
        const data = await getProductionCycleStatistics(param, conditionJson);
        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.mainPage.data = data.rows;
          this.mainPage.page.total = data.total;
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.mainPage.tableLoading = false;
      }
      // 加工时长(以秒为单位)
      // for (var i = 0; i < this.mainPage.data.length; i++) {
      //   this.mainPage.data[i].productionCycle = this.mainPage.data[i].productionCycle / 1000;
      // }
      // 计算加工时长
      // for (var i = 0; i < this.mainPage.data.length; i++) {
      //   var result = new Date(this.mainPage.data[i].recordEndTime) - new Date(this.mainPage.data[i].recordStartTime);
      //   this.mainPage.data[i].allWorkTime = result / 1000;
      //   console.log(this.mainPage.data[i].allWorkTime);
      // }
    },

    sortChange(property, order) {
      this.orderByData = {};
      this.orderByData[property] = order;
      this.handleMainPageChange(1, this.mainPage.page.pageSize);
    },
    propertyFormat({ cellValue }) {
      return cellValue + '%';
    },
    hourFormat({ cellValue }) {
      return (cellValue / 60).toFixed(2);
    },
    // 导出数据
    async exportTable() {
      try {
        this.mainPage.tableLoading = true;

        var conditionJson = {
          conditionJson1: [],
          operatorGroup: 1,
          conditionJson2: []
        };
        if (this.savedSearchCondition.length > 0) {
          conditionJson = searchCondetionFunc(...this.savedSearchCondition);
        }

        var param = {
          startTime: '',
          endTime: '',
          // shiftStart: '',
          // shiftEnd: '',
          size: 99999999,
          current: 1
        };

        if (this.dateTime) {
          param.startTime = this.dateTime[0];
          param.endTime = this.dateTime[1];
        }
        console.log(this.shiftData, this.shiftOptions, this.shiftOptions[this.shiftData]);
        if (this.shiftData !== null) {
          // eslint-disable-next-line no-unused-expressions
          param.shiftStart = this.shiftOptions[this.shiftData].shiftStart;
          param.shiftEnd = this.shiftOptions[this.shiftData].shiftEnd;
        }

        console.log(this.dateTime, this.dateTime2);

        // const data = await deviceIdleGetIdleProductReport(param, conditionJson);
        const data = await getProductionCycleStatistics(param, conditionJson);
        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          var arr = [];
          arr = data.rows.map((row) => ({
            ...row,
            ...row.deviceLiveReportVo
          }));
          this.$refs.xGrid.exportDatax(this.$store.state.pageInfo.pageConfigInfo.yieldKey + '生产周期报表', arr);
          // this.mainPage.page.total = data.total;
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.mainPage.tableLoading = false;
      }
    },
    /**
     * @description: 刷新表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableData(savedSearchCondition) {
      if (savedSearchCondition) {
        this.savedSearchCondition = [...savedSearchCondition];
        this.mainPage.page.currentPage = 1;
      }
      this.findList();
    },
    async getSystemShift() {
      try {
        const data = await configurationGetConfigurationByEnum({
          configurationTypeEnum: 'SHIFT_TIME'
        }, {});

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          console.log(data);
          if (data.rows.processConfiguration) {
            var jsonArr = JSON.parse(data.rows.processConfiguration);
            this.shiftOptions = [];
            this.shiftData = null;
            var index = 0;
            for (var key in jsonArr) {
              var json = {
                value: index,
                label: key + ' ' + jsonArr[key],
                shiftStart: jsonArr[key].split('-')[0],
                shiftEnd: jsonArr[key].split('-')[1]
              };
              ++index;
              this.shiftOptions.push(json);
            }
          }
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    // 获取车间数据
    async getGroup() {
      try {
        this.mainPage.tableLoading = true;
        const res = await GetList({},
          {
            conditionJson1: [
              {
                column: 'groupType', // 参数名
                operator: 1, // 并且
                ptype: 'int', // 参数类型
                type: 'eq', // 包含
                value: 2 // 参数值
              }
            ]
            // operator: this.searchData.groupOperator,
            // conditionJson2: this.searchData.conditionArr2
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.groupOptions = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    }
  },
  created() {
    this.getSystemShift();
    this.dateTime = [
      `${this.moment(new Date()).format('YYYY-MM-DD')} 00:00:00`,
      `${this.moment(new Date()).format('YYYY-MM-DD')} 23:59:59`
    ];
    // this.getQueryParameter();
    this.getGroup();
    this.findList();
  }
};

</script>
